<template>
	<div>
    <!--  -->
		<div class="login-body">
       <!--  -->
      <p class="title-box"></p>
      <!-- <p class="title-box"></p> -->
      <!--  -->
			<div class="login-box">
        <div class="head-img">
          <img src="../assets/images/user.png">
        </div>
				<el-form :model="loginForm" :rules="loginFormRules" ref="loginForm" label-width="0px">
					<el-form-item prop="username">
						<el-input v-model="loginForm.username" placeholder=""><i slot="prefix" class="el-input__icon el-icon-user"></i></el-input>
					</el-form-item>
					<el-form-item prop="password">
						<el-input type="password" placeholder="" v-model="loginForm.password"><i slot="prefix" class="el-input__icon el-icon-lock"></i></el-input>
					</el-form-item>
					<el-form-item prop="captcha" style="width:50%;margin-bottom:5px;display:inline-block;">
            <el-input type="captcha" placeholder="" v-model="loginForm.captcha" @keyup.enter.native="submitLoginForm('loginForm')"><i slot="prefix" class="el-input__icon el-icon-edit-outline"></i></el-input>
          </el-form-item>
          <!--  -->
          <div class="verification-box">
          </div>
          <el-checkbox v-model="checked"></el-checkbox>
					<div class="login-btn">
						<el-button type="warning" @click="submitLoginForm('loginForm')"></el-button>
					</div>
				</el-form>
			</div>
      <!--  -->
      <div class="ad-box">
        <img style="width:100%" src="../assets/images/login_imgL.png">
			</div>
		</div>
    <!--  -->
    <img class="bg-box" src="../assets/images/login_bg.png">
	</div>
</template>

<script>
	import particlesJS from '../assets/js/particles.js';
	export default {
		data: function () {
			return {
				loginForm: {
					username: '',
					password: '',
					captcha: '',
					path:'/index'
				},
				loginFormRules: {
					username: [
						{ required: true, message: '', trigger: 'blur' }
					],
					password: [
						{ required: true, message: '', trigger: 'blur' }
					]
				}
			}
		},
		methods: {
			submitLoginForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						this.$store.dispatch("updateUserinfo",this.loginForm);
					} else {
						this.$message.error('!');
						return false;
					}
				});
			}
		},
		beforeMount() {
		},
		mounted() {
			//https://github.com/VincentGarreau/particles.js
			particlesJS("particlesJS", {
				"particles": {
					"number": {
						"value": 30,
						"density": {
							"enable": true,
							"value_area": 800
						}
					},
					"color": {
						"value": "#ffffff"
					},
					"shape": {
						"type": "circle", // "circle", "edge" or "triangle"
						"stroke": {
							"width": 0,
							"color": "#fff"
						},
						"polygon": {
							"nb_sides": 5
						},
						"image": {
							"src": "",
							"width": 100,
							"height": 100
						}
					},
					"opacity": {
						"value": 0.8,
						"random": true,
						"anim": {
							"enable": false,
							"speed": 1,
							"opacity_min": 0.3,
							"sync": true
						}
					},
					"size": {
						"value": 6,
						"random": true,
						"anim": {
							"enable": false,
							"speed": 40,
							"size_min": 0.1,
							"sync": true
						}
					},
					"line_linked": {
						"enable": true,
						"distance": 150,
						"color": "#ffffff",
						"opacity": 0.4,
						"width": 1
					},
					"move": {
						"enable": true,
						"speed": 2,
						"direction": "none",
						"random": false,
						"straight": false,
						"out_mode": "out",
						"attract": {
							"enable": false,
							"rotateX": 600,
							"rotateY": 1200
						}
					}
				},
				"interactivity": {
					"detect_on": "canvas",
					"events": {
						"onhover": {
							"enable": true,
							"mode": "grab"
						},
						"onclick": {
							"enable": true,
							"mode": "push"
						},
						"resize": true
					},
					"modes": {
						"grab": {
							"distance": 200,
							"line_linked": {
								"opacity": 1
							}
						},
						"bubble": {
							"distance": 400,
							"size": 40,
							"duration": 2,
							"opacity": 8,
							"speed": 3
						},
						"repulse": {
							"distance": 200
						},
						"push": {
							"particles_nb": 2
						},
						"remove": {
							"particles_nb": 2
						}
					}
				},
				"retina_detect": true,
				"config_demo": {
					"hide_card": false,
					"background_color": "#b61924",
					"background_image": "",
					"background_position": "50% 50%",
					"background_repeat": "no-repeat",
					"background_size": "cover"
				}
			});
		}
	}

</script>

<style scoped>
	.login-header {
		position: absolute;
		top: 0;
		/*width: 100%;*/
		left:0;right:0;
		height: 60px;
		background-color: #fff;
		padding: 0 50px;
	}
	
	.login-footer {
		position: absolute;
		bottom: 0;
		left:0;right:0;
		/*width: 100%;*/
		height: 100px;
		padding: 10px 25px;
	}
	
	.login-footer p {
		color: #818a91;
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 8px;
	}
	
	.login-body {
		position: absolute;
		top: 0px;
		bottom: 0px;
		/*width: 100%;*/
    left:0;
    right:0;
    z-index: 999;
	}
  
  .title-box{
    text-align: center;
    color:#fff;
    padding-top: 40px;
    font-size: 2.8rem;
    font-weight: 600
  }

  .ad-box{
    position: absolute;
    top: 58%;
		left: 30%;
		transform: translate(-50%,-50%);
		width: 350px;
  }

	.login-box {
		position: absolute;
		display: inline-block;
		top: 60%;
		left: 70%;
		transform: translate(-50%,-50%);
		width: 300px;
		border-radius: 6px;
		background: rgba(255, 255, 255, 0.6)
	}
	
	.login-box form{
		padding: 20px;
  }
  
  .head-img{
    text-align: center;
    padding-top: 20px;
  }

  .head-img img{
    width:70px;
  }
	
	.login-title {
		padding: 15px;
		margin-bottom: 15px;
		text-align: center;
		font-size: 20px;
		color: #fff;
		border-top-left-radius: 6px;
		border-top-right-radius: 6px;
		background-color: #E35B5A;
  }
  
  .bg-box{
    width: 100%;
    min-width: 1300px;
    position: absolute;
    top: 0px;
    left: 0px;
  }
  
  .verification-box{
    width: 40%;
    height: 40px;
    background-color: rgb(255, 255, 255);
    display:inline-block;
    overflow: hidden;
    margin-bottom: -15px;
  }

	.login-btn {
		text-align: center;
	}
	
	.login-btn button {
		width: 100%;
		height: 36px;
	}
	
	.nav .nav-item {
		float: left;
		padding: 10px;
	}
	
	.nav .nav-link {
		font-size: 14px;
		line-height: 20px;
		color: rgba(0, 0, 0, .5);
		cursor: pointer;
	}
	
	.nav .nav-link:hover {
		color: rgba(0, 0, 0, .8);
	}
</style>


